/*************** SCROLLBAR BASE CSS ***************/

.scroll-wrapper {
    overflow: hidden !important;
    padding: 0 !important;
    position: relative;
}

.scroll-wrapper>.scroll-content {
    border: none !important;
    box-sizing: content-box !important;
    height: auto;
    left: 0;
    margin: 0;
    max-height: none;
    max-width: none !important;
    overflow: scroll !important;
    padding: 0;
    position: relative !important;
    top: 0;
    width: auto !important;
}

.scroll-wrapper>.scroll-content::-webkit-scrollbar {
    height: 0;
    width: 0;
}

.scroll-element {
    display: none;
}

.scroll-element,
.scroll-element div {
    box-sizing: content-box;
}

.scroll-element.scroll-x.scroll-scrollx_visible,
.scroll-element.scroll-y.scroll-scrolly_visible {
    display: block;
}

.scroll-element .scroll-bar,
.scroll-element .scroll-arrow {
    cursor: default;
}

.scroll-textarea {
    border: 1px solid #cccccc;
    border-top-color: #999999;
}

.scroll-textarea>.scroll-content {
    overflow: hidden !important;
}

.scroll-textarea>.scroll-content>textarea {
    border: none !important;
    box-sizing: border-box;
    height: 100% !important;
    margin: 0;
    max-height: none !important;
    max-width: none !important;
    overflow: scroll !important;
    outline: none;
    padding: 2px;
    position: relative !important;
    top: 0;
    width: 100% !important;
}

.scroll-textarea>.scroll-content>textarea::-webkit-scrollbar {
    height: 0;
    width: 0;
}




/*************** SCROLLBAR RAIL ***************/

.scrollbar-rail>.scroll-element,
.scrollbar-rail>.scroll-element div {
    border: none;
    margin: 0;
    overflow: hidden;
    padding: 0;
    position: absolute;
    z-index: 10;
}

.scrollbar-rail>.scroll-element {
    background-color: #06072f;
}

.faq-wrap>.scrollbar-rail>.scroll-element{
    background: none;
}

.join>.scrollbar-rail>.scroll-element{
    background-color: rgba($color: #06072f, $alpha: .2);
}

.scrollbar-rail>.scroll-element div {
    display: block;
    height: 100%;
    left: 0;
    top: 0;
    width: 100%;
}

.faq-wrap>.scrollbar-rail>.scroll-element div {
    left: 5px;
}

.scrollbar-rail>.scroll-element .scroll-element_size {
    background-color: #3134bc;

}

.scrollbar-rail>.scroll-element .scroll-element_outer:hover .scroll-element_size {
    background-color: #3134bc;
}

.faq-wrap>.scrollbar-rail>.scroll-element .scroll-element_size {
    background: none;

}

.faq-wrap>.scrollbar-rail>.scroll-element .scroll-element_outer:hover .scroll-element_size {
    background: none;
}

.join>.scrollbar-rail>.scroll-element .scroll-element_size {
    background: none;

}

.join>.scrollbar-rail>.scroll-element .scroll-element_outer:hover .scroll-element_size {
    background: none;
}

